import React from 'react';
import { Database, Workflow, Lock, LineChart } from 'lucide-react';

const cards = [
  {
    title: 'Centralized Data Management',
    desc: 'Manage all your business data in one secure, accessible platform.',
    icon: Database,
    gradientFrom: 'rgba(189, 13, 70, 0.2)',
    gradientTo: 'rgba(239, 0, 73, 0.08)',
  },
  {
    title: 'Workflow Automation',
    desc: 'Streamline your processes with intelligent automation tools.',
    icon: Workflow,
    gradientFrom: 'rgba(189, 13, 70, 0.08)',
    gradientTo: 'rgba(255, 41, 104, 0.08)',
  },
  {
    title: 'Enterprise Security',
    desc: 'Bank-level encryption and security protocols to protect your data.',
    icon: Lock,
    gradientFrom: 'rgba(158, 13, 60, 0.08)',
    gradientTo: 'rgba(189, 13, 70, 0.08)',
  },
  {
    title: 'Real-time Analytics',
    desc: 'Get instant insights with powerful analytics and reporting tools.',
    icon: LineChart,
    gradientFrom: 'rgba(239, 0, 73, 0.08)',
    gradientTo: 'rgba(189, 13, 70, 0.08)',
  },
];

export default function GradientCardShowcase() {
  return (
    <>
      <div className="flex justify-center items-center flex-wrap py-10 bg-white min-h-[600px]">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 max-w-6xl mx-auto px-4">
          {cards.map(({ title, desc, icon: Icon, gradientFrom, gradientTo }, idx) => (
            <div
              key={idx}
              className="group relative w-full h-[320px] transition-all duration-500"
            >
              {/* Skewed gradient panels */}
              <span
                className="absolute top-0 left-[50px] w-1/2 h-full rounded-lg transform skew-x-[15deg] transition-all duration-500 group-hover:skew-x-0 group-hover:left-[20px] group-hover:w-[calc(100%-90px)]"
                style={{
                  background: `linear-gradient(315deg, ${gradientFrom}, ${gradientTo})`,
                }}
              />
              <span
                className="absolute top-0 left-[50px] w-1/2 h-full rounded-lg transform skew-x-[15deg] blur-[30px] transition-all duration-500 group-hover:skew-x-0 group-hover:left-[20px] group-hover:w-[calc(100%-90px)]"
                style={{
                  background: `linear-gradient(315deg, ${gradientFrom}, ${gradientTo})`,
                }}
              />

              {/* Animated blurs */}
              <span className="pointer-events-none absolute inset-0 z-10">
                <span className="absolute top-0 left-0 w-0 h-0 rounded-lg opacity-0 bg-[rgba(255,255,255,0.1)] backdrop-blur-[10px] shadow-[0_5px_15px_rgba(0,0,0,0.08)] transition-all duration-100 animate-blob group-hover:top-[-50px] group-hover:left-[50px] group-hover:w-[100px] group-hover:h-[100px] group-hover:opacity-100" />
                <span className="absolute bottom-0 right-0 w-0 h-0 rounded-lg opacity-0 bg-[rgba(255,255,255,0.1)] backdrop-blur-[10px] shadow-[0_5px_15px_rgba(0,0,0,0.08)] transition-all duration-500 animate-blob animation-delay-1000 group-hover:bottom-[-50px] group-hover:right-[50px] group-hover:w-[100px] group-hover:h-[100px] group-hover:opacity-100" />
              </span>

              {/* Content */}
              <div className="relative z-20 left-0 p-[20px_40px] bg-[rgba(255,255,255,0.05)] backdrop-blur-[10px] shadow-lg rounded-lg text-gray-900 transition-all duration-500 group-hover:left-[-25px] group-hover:p-[40px_40px] h-full flex flex-col">
                <Icon className="w-12 h-12 mb-4 text-[#BD0D46]" />
                <h2 className="text-2xl font-bold mb-2 text-gray-900">{title}</h2>
                <p className="text-base leading-relaxed text-gray-700">{desc}</p>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Tailwind custom utilities for animation and shadows */}
      <style>{`
        @keyframes blob {
          0%, 100% { transform: translateY(10px); }
          50% { transform: translate(-10px); }
        }
        .animate-blob { animation: blob 2s ease-in-out infinite; }
        .animation-delay-1000 { animation-delay: -1s; }
        .shadow-[0_5px_15px_rgba(0,0,0,0.08)] { box-shadow: 0 5px 15px rgba(0,0,0,0.08); }
      `}</style>
    </>
  );
}
